import { GET_VIDEOGAMES, GET_VIDEOGAMES_NAME, GET_VIDEOGAME_ID, GET_GENRES, ADD_VIDEOGAME, FILTER_GENRE, ORDER_NAME, ORDER_RATING, SETING } from '../Actions/Constants';

const initialState = {
    videogames: undefined,
    videogamesCreate: null,
    videogameDetail: undefined,
    genres: []
}


export default function rootReducer(state = initialState, action) {
    switch (action.type) {
        case GET_VIDEOGAMES: {
            return {
                ...state,
                videogames: action.payload
            }
        }
        case GET_VIDEOGAME_ID: {
            return {
                ...state,
                videogameDetail: action.payload
            }
        }
        case GET_GENRES: {
            return {
                ...state,
                genres: action.payload
            }
        }
        case GET_VIDEOGAMES_NAME: {
            return {
                ...state,
                videogames: action.payload
            }
        }
        case ADD_VIDEOGAME: {
            return {
                ...state,
                videogamesCreate: action.payload
            }
        }
        
        case FILTER_GENRE: {
            return {
                ...state,
                videogames: action.payload
            }
        }
        case ORDER_NAME: {
            if (action.payload === "az") {
                return {
                    ...state, 
                    showVideoGames: [...state.showVideoGames].sort((game1, game2) => game1.name > game2.name ? 1 : -1)}
            }
                return {
                    ...state, 
                    showVideoGames: [...state.showVideoGames].sort((game1, game2) => game1.name > game2.name ? -1 : 1) }
        }
        case ORDER_RATING: {
            return {
                ...state,
                videogames: action.payload
            }
        }case SETING: {
            return {
                ...state,
                videogames: undefined
            }
        }        
        default: {
            return state;
        }
    }
}